@use'base/variables' as v;
@use'base/mixins' as m;


.productos{

    &__heading{
        @include m.headingCursiva;
        margin-bottom: 5rem;
    }

    &__grid{

        @include m.grid(1,2rem);

        @include m.tablet { 
            @include m.grid(2,2rem)
         }

         @include m.desktop { 
            @include m.grid(4,2rem)
          }
        
    }
}

.producto{
    &__contenido{
        background-color: v.$blanco;
        padding: 2rem;

    }

    &__imagen{
        width: 100%;
        height: auto;
        object-fit: cover;
    }

    &__nombre{
        @include m.heading;
        color: v.$secundario;
        font-size: 2rem;
    }

    &__descripcion{
        text-align: center;
        font-size: 1.4rem;
        text-transform: uppercase;
        line-height: 1.8;
    }
    &__precio{
        text-align: center;
        font-size: 2.4rem;
        color: v.$gris;
        margin: 0;
    }
}