@use'base/variables' as v;
@use'base/mixins' as m;


.cards{


    &__grid{
        @include m.tablet { 
            @include m.grid(3, 3rem)
         }

    }


}


.card{
max-width: 35rem;
margin: 0 auto 2rem auto;

&:last-of-type{
    margin-bottom: 0;
}
@include m.tablet { 
    max-width: unset;
    margin: 0;
 }

 &__imagen{
    border: .5rem solid v.$blanco;
    transition: transform .3s;

    &:hover{
        transform: rotate(4deg) scale(1.3);
    }

 }

 &__contenido{
    max-width: 90%;
    margin: 0 auto;
   
    background-color: v.$blanco;
    transform: translateY(-5rem)
 }

 &__heading{
    @include m.headingCursiva;
    font-size: 3.4rem;
    padding: 2rem;
 }
 &__enlace{
    @include m.boton(v.$secundario, v.$blanco);
    padding: 1rem 2rem;
    font-size: 1.8rem;
    display: block;
 }
}